.itemContainer {
  display: flex;
  padding: 10px;
  margin-top: 50px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: rgb(192, 240, 120);
  
}
.imageContainer {
  width: 100px;
  height: 100px;
  border-radius: 10px;
}
.image {
  max-width: 100%;
  height: 100%;
  border-radius: 20px;
}
.nameContainer {
  margin-left: 50px;
}
.name{
    font-size: larger;
    font-weight: bold;
}
.amount{
    margin-top: 10px;
    font-weight: bold;
}
