.recipeCard {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  color: #393e46;
  
  
}
.recipeName {
  font-size: 24px;
  margin-bottom: 10px;
}
.recipeImage {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}
.recipeDetails{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-evenly;
    /* align-items: center; */
}
.recipeInstructions{
    padding: 0;
    border-radius: 5px;
    color: rgb(122, 122, 123);
    font-family: Arial, Helvetica, sans-serif;
}
.recipeInstructions ol{
    margin-left: 0;
}
.recipeInstructions li{
    margin: 10px 0px;

}